import React from 'react';
export default class ResourceAdd extends React.Component {
    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e) {
        e.preventDefault();
        var form = document.forms.resourceAdd;
        this.props.createResource({
            category: form.category.value,
            name: form.name.value,
            address: {address1: form.address1.value, address2: form.address2.value, city: form.city.value, state: form.state.value, zip: form.zip.value},
            phone: form.phone.value,
            website: form.website.value,
            comments: form.comments.value,
            created: new Date()
        });
        //clear form
        form.category.value = ''; form.name.value = ''; 
        form.address1.value =''; form.address2.value =''; form.city.value =''; form.state.value =''; form.zip.value ='';
        form.phone.value = ''; form.website.value = ''; form.comments.value = '';
    }
    render() {
        return (
            <div>
                <form name="resourceAdd" onSubmit={this.handleSubmit}>
                    <input type="text" name="category" placeholder="Category" />
                    <input type="text" name="name" placeholder="Name" />
                    <input type="text" name="address1" placeholder="123 Fourth St" />
                    <input type="text" name="address2" placeholder="Suite 666" />
                    <input type="text" name="city" placeholder="Los Angeles" />
                    <input type="text" name="state" placeholder="CA" value="CA" disabled />
                    <input type="text" name="zip" placeholder="90033" />
                    <input type="text" name="phone" placeholder="(818) 555-5555" />
                    <input type="text" name="website" placeholder="http://website.com" />
                    <input type="text" name="comments" placeholder="Enter comment here"/>
                    <button>Add New Resource</button>
                </form>
            </div>
        )
    }
}