import React from 'react';
import 'whatwg-fetch';
import ResourceAdd from './ResourceAdd.jsx';
import ResourceFilter from './ResourceFilter.jsx';
class ResourceTable extends React.Component {
    render() {
        const resourceRows = this.props.resources.map(resource => <ResourceRow key={resource._id} resource={resource} />)
       // const borderedStyle = {border: "1px solid #e6e6e6", padding: 4};
        return (
            <table>
                <thead className="border-bottom">
                    <tr>
                        <th>Id</th>
                        <th>Category</th>
                        <th>Resource Name</th>
                        <th>Address</th>
                        <th>Phone</th>
                        <th>Website</th>
                        <th>Comments</th>
                        <th>Created</th>
                    </tr>
                </thead>
                <tbody>
                    {/* <ResourceRow resource_id={1}>Homeless Shelters LA</ResourceRow>
                    <ResourceRow resource_id={2}>Home for Aged People</ResourceRow> */}
                    {resourceRows}
                </tbody>
            </table>
        )
    }
}

class ResourceRow extends React.Component {
    render() {
        const resource = this.props.resource;
        const address = this.props.resource.address;
        return (
            <tr className="border-bottom">
                <th>{resource._id}</th>
                <td>{resource.category}</td>
                <td>{resource.name}</td>
                <td>{address.address1} {address.address2} {address.city}, {address.state} {address.zip}</td>
                <td>{resource.phone}</td>
                <td>{resource.website}</td>
                <td>{resource.comments}</td>
                <td>{resource.created ? resource.created.toDateString(): ''}</td>
            </tr>
        )
    }
}

export default class ResourceList extends React.Component {
    constructor() {
        super();
        this.state = {resources:[]};
        this.createResource = this.createResource.bind(this);
    }

    componentDidMount() {
        this.loadData();
    }

    loadData()  {
        fetch('api/resources').then(response => {
            if (response.ok) {
                response.json().then(data => {
                    console.log('Total count of records:', data._metadata.total_count);
                    data.records.forEach(resource => {
                        resource.created = new Date(resource.created);
                    });
                    this.setState({resources: data.records});
                })
            } else {
                response.json().then(error => {
                    alert("Failed to fetch issues:" + error.message);
                });
            }
        }).catch(err => {
            alert("Error in fetching the data from server:", err);
        });
    }

    createResource(newResource) {
       fetch('/api/resources', {
           method:'POST',
           headers: {'Content-Type':'application/json'},
           body: JSON.stringify(newResource),
       }).then( response => {
           if (response.ok) {
               response.json().then(updatedResource => {
                   updatedResource.created = new Date(updatedResource.created);
                   const newResources = this.state.resources.concat(updatedResource)
                   this.setState({resources: newResources});
               });
           } else {
               response.json().then(error => {
                   alert ("Failed to add resource: " + error.message);
               });
           }
       }).catch (err => {
           alert('Error in sending data to the server: '+ err.message);
       });

        // response.json()
        // ).then(updatedResource => {
        //     updatedResource.created = new Date(updatedResource.created);
        //     const newResources = this.state.resources.concat(updatedResource);
        //     this.setState({ resources: newResources});
        // }).catch(err => {
        //     alert("error in sending data to server: " + err.message);
        // });
    }

       render() {
        return (
            <div>
                <h1>Reources</h1>
                <ResourceFilter />
                <hr />
                <ResourceTable resources={this.state.resources} />
                <hr />
                <ResourceAdd createResource={this.createResource} />
            </div>
        )
    }
}